var React = require("react");

class EditParcelStatus extends React.Component {
    constructor(props) {
        super(props);
        
        let s = '';
        if ( this.props.parcel ) {
            s = this.props.parcel.parcelStatusCode;
        }
        this.state = { myStatus: s }

        this.saveData = this.saveData.bind(this);
        this._statusChange = this._statusChange.bind(this);
    }

    saveData() {
        var val = this.state.myStatus;
        this.props.saveData("edit-parcel-status", { status: val });
    }

    render() {
        let editStatus = (this.props.status != null) ? this.props.status : "";

        return (
            <div id="edit-roe-status-overlay" className="shorter">
                <div className="panel-header">
                    <img src="assets/images/icon-add.png" alt="" aria-hidden="true" />
                    <h2>Edit Acquisition Status</h2>
                    <span className="panel-close" onClick={ this.props.setOverlay.bind(null, "none", null) }>X</span>
                </div>
                <div className="overlay-container">

                    <div className="panel-input">
                        <label htmlFor="edit-roe-status">Acquisition Status:</label>
                        <select id="edit-roe-status" name="edit-roe-status" value={ this.state.myStatus } onChange={ this._statusChange } >
                            <option id="default" value="-">select a status...</option>
                            <option id="Offer_Made" value="Offer_Made">Offer Made</option>
                            <option id="Offer_Denied" value="Offer_Denied">Offer Denied</option>
                            <option id="Easement_Signed" value="Easement_Signed">Easement Signed</option>
                            <option id="Compensation_Check_Cut" value="Compensation_Check_Cut">Compensation Check Cut</option>
                            <option id="Documents_Recorded" value="Documents_Recorded">Documents Recorded</option>
                            <option id="Compensation_Received_by_Owner" value="Compensation_Received_by_Owner">Compensation Received by Owner</option>
                        </select>
                    </div>
                    
                    <div className="overlay-form-buttons">
                        <a href="javascript:void(0);" onClick={ this.props.setOverlay.bind(null, "none", null) }>Cancel</a>
                        <a href="javascript:void(0);" onClick={ this.saveData } className="red-button"><img src="assets/images/icon-save.png" alt="" aria-hidden="true" /><span>Save</span></a>
                    </div>

                </div>
            </div>
        );
    }

    _statusChange(evt) {
        let s = evt.target.value;
        if ( s !== "-")
            this.setState({ myStatus: s });
    }
}

module.exports = EditParcelStatus;
