import * as React from 'react';
import * as d3 from 'd3';
import { arc, pie } from 'd3-shape';

interface CircleGraphProps {
    title: string;
    width: number;
    height: number;
    radius: number;
    data: Array<any>;
    colors: Array<string>;
}

class CircleGraph extends React.Component<CircleGraphProps, {}>{

    constructor(props:CircleGraphProps) {
        super(props);
    }

    public render() {

        const data = this.props.data;
        // const data = [ { classification: "no_activity", count: 30 }, { classification: "what", count: 10 }]

        const t = this.props.title;
        const w = this.props.width;
        const h = this.props.height;
        const r = this.props.radius;
        const c = this.props.colors;

        const transform = `translate( ${ w/4}, ${ h /2 })`
        const color = d3.scaleOrdinal().range(c);

        const arc = d3.arc().outerRadius(r).innerRadius(0);
        const pieE = d3.pie().value( (d:any) => d.count ).sort(null);
        const pie = pieE(data).map( (d:any,i:number) => { return (
            <path key={i} fill={ color(i) } d={ arc(d) } />
        )}); 

        return (
            <div>
                <svg id={ 'circlexxx' } width={w} height={h}> 

                    <g transform={transform}>
                        {pie}
                    </g>
                </svg>
            </div>
        );
    }
}

export = CircleGraph;