var React = require("react");
var CircleGraph = require("../Display/CircleGraph");
var GraphLegend = require("../Display/GraphLegend");

var parColors = ['#828282', '#ffebaf', '#ffaa00', '#ffff00', '#ff0000', '#55ff00', '#6677cd', '#df72ff', '#8400a8'];
var roeColors = ["#ccc", "#0070ff", "#d1ff73", "#5f0", "#ff0", "#e60000"];
var accColors = ["#828282", "#cd6666", "#5f0"];
class StatsPanel extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {console.log(this.props)
        if( this.props.roeArray != [] ) {
            var deg, angle, currDegTotal;
            var total = 0, idx = -1, degTotal = 0;
            var amt = this.props.roeArray;
            var side = 220;
            var clrs = ["#ccc", "#0070ff", "#5f0", "#ff0", "#e60000", "#d1ff73"];

            for(var i = 0; i < amt.length; i++) { total += amt[i]; }
        }

        return (
            <div id="home-panel">
				<div id="stat-numbers">
					<div onClick={ this.props.changePanel.bind(null, "parcel", "055150000000100C" )}>
						<img src="assets/images/icon-parcels.png" alt="" aria-hidden="true" />
						<h2>TOTAL PARCELS</h2>
						<span>{ this.props.parcelTotal }</span>
					</div><div>
						<img src="assets/images/icon-owners.png" alt="" aria-hidden="true" />
						<h2>TOTAL OWNERS</h2>
						<span>{ this.props.ownerTotal }</span>
					</div>
				</div>
				<div id="stat-graphs">
                    <h3>PARCEL STATUS</h3>

                    <div id="parcel-status-graph">
                        <CircleGraph width={400} height={220} radius={100} data={this.props.roeArray} colors={roeColors} />
                    </div>
                    <GraphLegend data={this.props.roeArray} colors={roeColors} />

                    <h3>ACCESS LIKELIHOOD</h3>
                    <div id="parcel-status-graph">
                        <CircleGraph width={400} height={220} radius={100} data={this.props.ratingArray} colors={accColors} />
                    </div>
                    <GraphLegend data={this.props.ratingArray} colors={accColors} />
				</div>
			</div>
        );
    }
}

module.exports = StatsPanel;
