import * as React from 'react';


interface GraphLegendProps {
    data: Array<any>;
    colors: Array<any>;
}

class GraphLegend extends React.PureComponent<GraphLegendProps, {}>{

    constructor(props:GraphLegendProps) {
        super(props);
    }

    public render () {

        const clrs = this.props.colors;
        let legend = {};
        if ( this.props.data ) {
            legend = this.props.data.map( (x,idx) => {
                return (
                    <span key={idx} ><span style={{ backgroundColor: clrs[idx], display: 'inline-block', height: '16px', marginRight: '5px', verticalAlign: 'middle', width: '16px' }}></span>{x.caption}: {x.count}</span>
                );
            });
        }

        return (
            <div className="graph-key">
            {legend}
            </div>
        );
    }
}

export = GraphLegend;